<template>
  <div id="list" class="list-group">
    <span class="text-center list-group-item"></span>
<!--    
      to 
          name  route/index.js
          params 
-->
<!--    v-for 
            forum
            for(Object obj:objs)
  -->
<!--    {{ forum.floorNum }}
          data 
  -->
    <router-link :to="{name:'p',params:{fid:forum.fid}}" tag="a" class="list-group-item" v-for="forum in forums" :key="forum.fid">
      <span class="badge">
        {{ forum.floorNum }}
      </span>
      {{ forum.title }}
    </router-link>
  </div>
</template>

<script>
export default {
  name: "ListGroup",
  data(){
    return{
      forums:[]
    }
  },
  methods:{

  },
  // 
  created(){
    let _this = this;
    // 
    this.$http.get("http://localhost:8011/forum/getForumRandom").then((res)=>{
      _this.forums = res.data;
    })
  }
}
</script>

<style scoped>
#list{
  display: inline;
}
a{
  cursor:pointer;
}
</style>